.aether__features {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}

.aether__features-heading {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;

    text-align: left;
    margin-right: 5rem;
}

.aether__features-heading h1 {
    font-size: 34px;
    line-height: 45px;
    font-weight: 800;
    font-family: var(--font-family);
}

.aether__features-container {
    flex: 1.5;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}

@media screen and (max-width: 990px) {
    .aether__features {
        flex-direction: column;
    }

    .aether__features-heading {
        margin: 0 0 2rem 0;
    }
}


@media screen and (max-width: 550px) {
    .aether__features-heading h1 {
        font-size: 28px;
        line-height: 38px;
    }
}

.aether__buttons {
    cursor: pointer;
    outline: none;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    transition: all 0.2s ease-in-out;
}

.aether__buttons:hover {
    transform: scale(1.2);
    text-shadow: white 0px 0px 20px 0px;
}